'use client';

import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { createPortal } from 'react-dom';
import { signInWithGoogle } from '@/lib/supabase/auth';
import { useAppContext } from '@/context/AppContext';

interface ElegantAuthModalProps {
  isOpen: boolean;
  onClose: () => void;
}

const ElegantAuthModal: React.FC<ElegantAuthModalProps> = ({ isOpen, onClose }) => {
  const [mounted, setMounted] = useState(false);
  const { loading, setError } = useAppContext();

  useEffect(() => {
    setMounted(true);
  }, []);

  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = '';
    }
    return () => {
      document.body.style.overflow = '';
    };
  }, [isOpen]);

  const handleGoogleSignIn = async () => {
    try {
      setError('auth', null);
      onClose();
      await new Promise(resolve => setTimeout(resolve, 100));
      await signInWithGoogle();
    } catch (error: any) {
      console.error('Google sign-in error:', error);
      setError('auth', error.message || 'Google authentication failed. Please try again.');
    }
  };

  if (!mounted || !isOpen) return null;

  const modalVariants: any = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        duration: 0.3
      }
    },
    exit: {
      opacity: 0,
      transition: {
        duration: 0.2
      }
    }
  };

  const contentVariants: any = {
    hidden: {
      opacity: 0,
      scale: 0.9,
      y: 20
    },
    visible: {
      opacity: 1,
      scale: 1,
      y: 0,
      transition: {
        duration: 0.4,
        type: "spring",
        stiffness: 300,
        damping: 30
      }
    },
    exit: {
      opacity: 0,
      scale: 0.95,
      y: -10,
      transition: {
        duration: 0.2
      }
    }
  };

  return createPortal(
    <AnimatePresence mode="wait">
      <motion.div
        className="fixed inset-0 z-[99999] flex items-center justify-center bg-black/60 backdrop-blur-sm"
        variants={modalVariants}
        initial="hidden"
        animate="visible"
        exit="exit"
        onClick={onClose}
      >
        <motion.div
          className="relative mx-4 w-full max-w-md overflow-hidden rounded-2xl border border-white/20 bg-white shadow-2xl"
          variants={contentVariants}
          onClick={(e) => e.stopPropagation()}
        >
          {/* Elegant gradient border effect */}
          <div className="absolute inset-0 -z-10 bg-gradient-to-br from-amber-400 via-amber-500 to-amber-600 opacity-90" />
          
          {/* Pattern overlay */}
          <div className="absolute inset-0 opacity-5" 
               style={{
                 backgroundImage: `url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23000' fill-opacity='1'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E")`
               }} 
          />

          {/* Header with elegant typography */}
          <div className="relative px-8 pt-10 pb-8 text-center bg-white/95 backdrop-blur-sm">
            <motion.button
              onClick={onClose}
              className="absolute right-4 top-4 flex h-10 w-10 items-center justify-center rounded-full bg-white/80 text-gray-600 shadow-md transition-all duration-300 hover:bg-white hover:text-gray-900 hover:rotate-90"
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.95 }}
              aria-label="Close modal"
            >
              <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </motion.button>

            <motion.div
              initial={{ opacity: 0, y: 10 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.1, duration: 0.5 }}
            >
              <h2 className="font-serif text-3xl font-semibold tracking-tight text-gray-900 mb-3">
                Welcome Back
              </h2>
              <p className="font-sans text-base font-medium text-gray-600">
                Sign in to access your exclusive design experience
              </p>
            </motion.div>
          </div>

          {/* Content area with refined styling */}
          <div className="px-8 pb-10 pt-6 bg-white/95 backdrop-blur-sm">
            {/* Error display */}
            <AnimatePresence>
              {loading?.auth === false && (
                <motion.div
                  initial={{ opacity: 0, height: 0 }}
                  animate={{ opacity: 1, height: 'auto' }}
                  exit={{ opacity: 0, height: 0 }}
                  className="overflow-hidden"
                >
                  {/* Space for error messages */}
                </motion.div>
              )}
            </AnimatePresence>

            {/* Google Sign In Button with elegant styling */}
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 0.2, duration: 0.5 }}
            >
              <button
                onClick={handleGoogleSignIn}
                disabled={loading?.auth}
                className="group relative w-full overflow-hidden rounded-xl border border-gray-200 bg-white px-6 py-5 font-sans font-medium text-gray-700 shadow-lg transition-all duration-300 hover:shadow-xl hover:-translate-y-0.5 disabled:cursor-not-allowed disabled:opacity-70"
              >
                {/* Hover gradient overlay */}
                <div className="absolute inset-0 -z-0 bg-gradient-to-r from-amber-50 to-amber-100 opacity-0 transition-opacity duration-300 group-hover:opacity-100" />
                
                <div className="relative z-10 flex items-center justify-center">
                  <div className="relative mr-4 flex h-6 w-6 items-center justify-center">
                    <div className="absolute inset-0 rounded-full bg-white shadow-sm" />
                    <svg viewBox="0 0 24 24" className="relative h-5 w-5">
                      <path d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z" fill="#4285F4" />
                      <path d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z" fill="#34A853" />
                      <path d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z" fill="#FBBC05" />
                      <path d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z" fill="#EA4335" />
                    </svg>
                  </div>
                  
                  <span className="text-base font-semibold tracking-wide">
                    {loading?.auth ? 'Signing in...' : 'Continue with Google'}
                  </span>
                  
                  {loading?.auth && (
                    <motion.div
                      initial={{ opacity: 0 }}
                      animate={{ opacity: 1 }}
                      className="absolute right-4 top-1/2 -translate-y-1/2"
                    >
                      <div className="h-5 w-5 animate-spin rounded-full border-2 border-gray-200 border-t-amber-500" />
                    </motion.div>
                  )}
                </div>
              </button>
            </motion.div>

            {/* Elegant divider */}
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 0.3, duration: 0.5 }}
              className="my-8"
            >
              <div className="relative">
                <div className="absolute inset-0 flex items-center">
                  <div className="w-full border-t border-gray-200" />
                </div>
                <div className="relative flex justify-center">
                  <span className="bg-white/95 px-4 font-serif text-sm font-medium tracking-wide text-gray-500">
                    Exclusive Access
                  </span>
                </div>
              </div>
            </motion.div>

            {/* Feature list with elegant icons */}
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 0.4, duration: 0.6 }}
              className="space-y-4"
            >
              <div className="flex items-center">
                <div className="mr-4 flex h-10 w-10 flex-shrink-0 items-center justify-center rounded-full bg-gradient-to-br from-amber-100 to-amber-200 shadow-sm">
                  <svg className="h-5 w-5 text-amber-700" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                  </svg>
                </div>
                <span className="font-sans text-sm font-medium text-gray-700">Personalized design recommendations</span>
              </div>
              
              <div className="flex items-center">
                <div className="mr-4 flex h-10 w-10 flex-shrink-0 items-center justify-center rounded-full bg-gradient-to-br from-amber-100 to-amber-200 shadow-sm">
                  <svg className="h-5 w-5 text-amber-700" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 3h2l.4 2M7 13h10l4-8H5.4M7 13L5.4 5M7 13l-2.293 2.293c-.63.63-.184 1.707.707 1.707H17m0 0a2 2 0 100 4 2 2 0 000-4zm-8 2a2 2 0 11-4 0 2 2 0 014 0z" />
                  </svg>
                </div>
                <span className="font-sans text-sm font-medium text-gray-700">Save your favorite pieces to collections</span>
              </div>
              
              <div className="flex items-center">
                <div className="mr-4 flex h-10 w-10 flex-shrink-0 items-center justify-center rounded-full bg-gradient-to-br from-amber-100 to-amber-200 shadow-sm">
                  <svg className="h-5 w-5 text-amber-700" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                  </svg>
                </div>
                <span className="font-sans text-sm font-medium text-gray-700">Priority booking for consultations</span>
              </div>
            </motion.div>

            {/* Footer note */}
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 0.5, duration: 0.5 }}
              className="mt-8 pt-6 border-t border-gray-200"
            >
              <p className="text-center font-sans text-xs font-medium text-gray-500">
                By continuing, you agree to our <span className="text-amber-700 hover:text-amber-800 cursor-pointer transition-colors">Terms of Service</span> and <span className="text-amber-700 hover:text-amber-800 cursor-pointer transition-colors">Privacy Policy</span>
              </p>
            </motion.div>
          </div>
        </motion.div>
      </motion.div>
    </AnimatePresence>,
    document.body
  );
};

export default ElegantAuthModal;